@import url(https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;700&display=swap);
body{font-family:"Inter",sans-serif}h1,h2,h3,h4,h5,h6{font-weight:700}h1{font-size:6rem}h2{font-size:5rem}h3{font-size:4rem}h4{font-size:3rem}h5{font-size:2rem}h6{font-size:1.5rem}p,a,span{font-size:1rem;font-weight:400;letter-spacing:0.3px}.navbar{background-color:#fff;color:#fff;max-width:50%;margin-left:auto;padding:1rem;border-radius:50px;margin-top:1rem;margin-right:1rem;box-shadow:rgba(17,17,26,0.1) 0px 0px 16px}.navbar ul{display:flex;list-style:none}.navbar ul a{text-decoration:none;color:#2d3436;margin-right:20px}body{margin:0;padding:0;box-sizing:border-box;-webkit-font-smoothing:antialiased}.page-wrapper{max-width:90%;margin:0 auto}.hero-section{margin-top:8rem;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;color:#2d3436;text-align:center}.hero-section h1{color:#b33771}.hero-section .content-container{display:flex;justify-content:center}.hero-section .content-container .column{width:45%;height:75vh}.hero-section .content-container .column h3{font-weight:300;margin-bottom:7rem}.hero-section .content-container .column .button{text-decoration:none;color:#2d3436;text-transform:uppercase;font-weight:500;color:#b33771;border:3px solid #b33771;padding:15px 8px;border-radius:255px 15px 225px 15px/15px 225px 15px 255px}

